
import { $langs } from '@/plugins/translations';
import { LangType } from '@/types';
import { defineComponent } from 'vue';

export default defineComponent({
  emits: ['label:update', 'dialog:closed'],
  props: {
    // Label name
    name: {
      type: Object as () => LangType,
      default: () => {
        return $langs('site');
      },
    },
    // Label color
    color: {
      type: String,
      default: '',
    },
    // Title dialog
    title: {
      type: String,
      default: '',
    },
    // Label id from delete / edit
    labelId: {
      type: Number,
      default: null,
    },
    // Rating id
    ratingId: {
      type: Number,
      default: null,
    },
    // Action type for back-end query
    actionType: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      // Errors
      errors: {
        name: '',
        color: '',
      },
      // Loading data
      isLoading: false,
      // Rating label
      label: {
        name: this.$langs('site'),
        color: '',
      },
      // Default colors
      colorsListDefault: this.$config['labels'].colorsListDefault,
    };
  },
  created() {
    this.label.name = { ...this.name };
    this.label.color = this.color || this.$config['labels'].colorDefault;
  },
  methods: {
    // Set color
    setColor(color: string) {
      this.label.color = color;
    },

    // Create rating label
    async createLabel() {
      if (this.isLoading) return;
      this.isLoading = true;
      this.$utils.clearErrors(this.errors, this.errors);

      try {
        await this.$api['labels'].createLabel({
          color: this.label.color,
          name: this.label.name,
          ratingId: this.ratingId,
        });

        this.$utils.showMessageSuccess({
          message: this.$t('Created'),
        });

        this.$emit('label:update', { event: 'create' });
      } catch (errors: any) {
        let isValidationError = this.$utils.setErrors(this.errors, errors.errors);
        if (!isValidationError) {
          this.$utils.showMessageError({ message: errors.server, errors });
        }
      } finally {
        this.isLoading = false;
      }
    },

    // Edit rating label
    async editLabel() {
      if (this.isLoading) return;
      this.isLoading = true;
      this.$utils.clearErrors(this.errors, this.errors);

      try {
        await this.$api['labels'].editLabel({
          labelId: this.labelId,
          color: this.label.color,
          name: this.label.name,
          ratingId: this.ratingId,
        });

        this.$utils.showMessageSuccess({
          message: this.$t('Changed'),
        });

        this.$emit('label:update', { event: 'edit' });
      } catch (errors: any) {
        let isValidationError = this.$utils.setErrors(this.errors, errors.errors);
        if (!isValidationError) {
          this.$utils.showMessageError({ message: errors.server, errors });
        }
      } finally {
        this.isLoading = false;
      }
    },

    // Delete label
    async deleteLabel() {
      await this.$utils.showDialogConfirm({
        title: this.$t('Are you sure you want to delete?'),
      });
      if (this.isLoading) return;
      this.isLoading = true;

      try {
        await this.$api['labels'].deleteLabel({
          labelId: this.labelId,
          ratingId: this.ratingId,
        });

        this.$utils.showMessageSuccess({
          message: this.$t('Removed'),
        });

        this.$emit('label:update', { event: 'delete' });
        this.$emit('dialog:closed');
      } catch (errors: any) {
        this.$utils.showMessageError({ message: errors.server, errors });
      } finally {
        this.isLoading = false;
      }
    },
  },
});
